.go-top {
  cursor: pointer;
  fill: #ff7400;
  visibility: hidden;
  opacity: 0;
  z-index: 101;
  background-color: #5a5a5a;
  border: 10px solid #5a5a5a;
  border-radius: 50%;
  transition: opacity .5s, visibility .5s;
  display: flex;
  position: fixed;
  bottom: 10px;
  right: 10px;
}

.go-top:hover, .go-top:focus {
  filter: brightness(1.5);
  transition: all .2s;
}

.go-top-show {
  opacity: 1;
  visibility: visible;
  z-index: 100;
  transition: opacity .5s, visibility .5s;
}

