.hero__backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .4s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.hero__backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.hero__backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(0);
}

.modal {
  height: 100%;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  display: flex;
}

.modal__container {
  max-width: 400px;
  width: 100%;
  max-height: 609px;
  height: 100%;
  z-index: 2;
  background-color: #fff;
  border-radius: 12px;
  padding: 40px;
  position: relative;
  overflow-y: auto;
}

@media screen and (min-width: 1200px) {
  .modal__container {
    max-width: 528px;
    max-height: 581px;
  }
}

.modal__title {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 24px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.17;
}

.modal__text-data {
  text-align: center;
  margin-bottom: 14px;
  font-size: 12px;
}

.button__close {
  width: 35px;
  height: 35px;
  fill: wheat;
  cursor: pointer;
  background-color: gray;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: transfom .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
  position: absolute;
  top: 16px;
  right: 16px;
  transform: scale(1);
}

.button__close:hover, .button__close:focus {
  fill: #00f;
}

.hero__backdrop.is-hidden .button__close {
  transform: scale(0);
}

.modal__label {
  letter-spacing: .01em;
  color: gray;
  margin-bottom: 8px;
  font-size: 12px;
  line-height: 1.17;
  display: block;
}

.modal__field {
  margin-bottom: 10px;
  position: relative;
}

.modal__input {
  width: 100%;
  height: 40px;
  border: 1px solid #000;
  border-radius: 4px;
  padding: 0 42px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.modal__input:focus-within {
  border-color: #00f;
  outline: none;
}

.modal__input:focus-within + .modal__icon {
  fill: #00f;
}

.modal__icon {
  fill: #000;
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

.modal__coment {
  resize: none;
  width: 100%;
  height: 120px;
  border: 1px solid gray;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 12px 16px;
