.inventory-image {
  margin-bottom: 16px;
  margin-left: auto;
  margin-right: auto;
}

.inventory-item-title {
  margin-bottom: 16px;
  font-size: 22px;
}

.inventory-item-descr {
  margin-bottom: 60px;
  font-size: 14px;
}

.contraindication {
  padding: 60px 20px;
}

.contraindication-list {
  flex-wrap: wrap;
  gap: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.contraindication-item {
  max-width: 158px;
  flex-direction: column;
  display: flex;
}

.contraindication-foto {
  margin-bottom: 12px;
}

.contraindication-subtitle {
  color: #272727;
  margin-bottom: 6px;
  font-size: 14px;
  line-height: 1.5;
}

.contraindication-descr {
  color: #000;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.5;
}

.package {
  background-color: rgba(143, 141, 141, .9);
}

.package-list {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.package-item {
  background-color: rgba(248, 242, 242, .6);
  border: .15vw solid #e6e6e6;
  border-radius: 25px;
  padding: 10px;
}

.package-item-title-box {
  text-align: center;
  color: wheat;
  background-color: #424346;
  border-radius: 25px;
  margin-bottom: 12px;
  padding: 20px;
  font-size: 40px;
}

.block-2 {
  background-color: rgba(255, 255, 255, .3);
  border: .15vw #e6e6e6;
  border-radius: 25px;
  margin-bottom: 16px;
  padding: 20px;
  box-shadow: 0 0 12px rgba(0, 0, 0, .3);
}

.package-item-descr {
  margin-bottom: 8px;
}

.package-price {
  text-align: center;
  text-transform: uppercase;
  margin: 32px 0;
  font-size: 32px;
  font-weight: 600;
}

.contact {
  background-color: beige;
}

.form-contact {
  max-width: 440px;
  margin-left: auto;
  margin-right: auto;
}

